.header {
  display: flex;
  justify-content: center;
  align-items: center;

  padding: 40px 0;
  min-height: 695px;

  background-color: var(--header-color);
  background-image: url("./../../img/header-bg.png");
  background-repeat: no-repeat;
  background-size: auto;
  background-position: center;
  color: white;
  text-align: center;
}

.header__wrapper {
  padding: 0 15px;
  max-width: 660px;
}

.header__title {
  margin-bottom: 20px;

  font-size: 40px;
  font-weight: 700;
  line-height: 1.4;
  color: var(--text-title);
}
.header__title strong {
  font-size: 60px;
}
.header__title em {
  font-style: normal;
  color: var(--header-title__main);
}

.header__text {
  margin-bottom: 40px;

  font-size: 18px;
  line-height: 1.333;
}
.header__text p + p {
  margin: 1em;
}
